import React, {useEffect, useState} from "react";
import ReactPlayer from 'react-player'
import styled from "styled-components";

const aa = require("../assets/voide/vodie.mp4").default;
export const VodieWidget: React.FC = () => {

    const [height, setHeight] = useState(810);
    const [width, setwidth] = useState(0);
    const [postion, setPostion] = useState({left: 0, top: 0})

    let minHeight = 810;
    let minWidth = 1440;
    useEffect(() => {
        let _height = document.body.clientHeight;
        let _width;
        let _postion = {
            left: 0,
            top: 0,
        };

        _width = _height * 1.7777;
        if (_height < 500) {
            setHeight(minHeight);
            _postion.top = (minHeight - _height) / 2;
            setwidth(minWidth);
        } else {
            setHeight(_height);
            setwidth(_width);
            _postion.left = (_width - document.body.clientWidth) / 2;
        }

        setPostion(_postion);

    }, [])
    return (
        <VodieStyle>
            <video className={'video'} style={{
                height: height + "px",
                width: width + 'px',
                left: (0-postion.left) +"px",
                top:(0-postion.top)+"px"
            }} loop autoPlay muted
                   playsInline>
                <source src={aa}/>
            </video>
            {/*<ReactPlayer  muted={true} playing={true} url={aa} loop={true} controls={false}/>*/}

        </VodieStyle>
    );
}
const VodieStyle = styled.div`
  .video {
    pointer-events: none;
    position: fixed;
  }

`
